import * as React from "react";
import {Link} from 'gatsby';
import Constants from '../../utils/constants';

const NextDrops = ({background} : {background: string}) => {
    const today = new Date();
    return (
        <section className={"tf-section tf-item tf-mint " + background}>
            <div className="container">
                <div className="row">
                    <div className="col-md-12 center subtitle">
                        <h5>Mint a C-Shirt and get free-mint option for the next drops</h5>
                        <p className="sub-title">Only C-Shirts holders can have free-mint and airdrops on every tournament</p>
                    </div>
                </div>
                <div className="row next-mints">
                    <div className="col-md-12 center">
                        {Constants.TOURNAMENTS.filter((t:any) => { return t.date > today}).map((tournament:any) => (
                            <Link to={tournament.url} key={tournament.url}>
                                <div className="box-text corner-box">
                                    <div><strong>{tournament.name}</strong></div>
                                    <p className="alignLeft">
                                        Date: {tournament.date ? tournament.date.toUTCString() : 'TBD'}<br />
                                        Total supply: {tournament.totalSupply != 0 ? tournament.totalSupply : 'TBD'}<br />
                                        Private sale: {tournament.privateSalePrice}<br />
                                        Public sale: {tournament.publicSalePrice}<br />
                                        Gamification: {tournament.gamification}% of sold (in ETH)
                                    </p>
                                </div>    
                            </Link>                    
                        ))}
                    </div>
                </div>
            </div>
        </section>
    );
};

export default NextDrops;