import React from 'react';
import ProgressBar from "@ramonak/react-progress-bar";
import sampleImage from '../../assets/images/empty.png';

const Supply = ({privateSale, privateSaleSupply, publicSale, publicSaleSupply, price} : {privateSale:number, privateSaleSupply:number, publicSale:number, publicSaleSupply:number, price: string}) => {        
    return (
        <div>
            <div className="image-details">
                <img src={sampleImage} alt="C-Shirts" />
            </div>
            <div className="image-details">
                <h4>TOTAL SUPPLY: {publicSaleSupply}</h4>
                <h5>Price: {price} ETH</h5>
                <div className="row">
                    <div className="col-md-12">
                        Private sale: {privateSale} of {privateSaleSupply}
                    </div>
                    <div className="col-md-12 progressBar">
                        <ProgressBar completed={Math.floor((privateSale / privateSaleSupply) * 100)}                                     
                                     className="wrapperProgressBar"
                                     barContainerClassName="containerProgressBar"                                     
                                     labelClassName="labelProgressBar"
                                     bgColor="#3B673C"
                                     height="37px" />
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        Public sale: {publicSale} of {publicSaleSupply - privateSaleSupply}
                    </div>
                    <div className="col-md-12 progressBar">
                        <ProgressBar completed={Math.floor((publicSale / publicSaleSupply) * 100)} 
                                     className="wrapperProgressBar"
                                     barContainerClassName="containerProgressBar"                                     
                                     labelClassName="labelProgressBar"
                                     bgColor="#3B673C"
                                     height="37px" />
                    </div>
                </div>                
            </div>
        </div>
    );
};

export default Supply;