import React, { useState, useTransition } from 'react';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import signs from '../assets/contracts/signatures/cshirts-privatesale.json';
import { BigNumber, ethers } from "ethers";
import SaleType from '../model/saleType';
import Constants from '../utils/constants';
import { Deferrable } from 'ethers/lib/utils';
import MetamaskIcon from '../assets/images/metamask.png';
import NextDrops from '../components/mint/nextdrops';
import FAQ from '../components/mint/faq';
import Supply from '../components/mint/supply';
import PreMintMessage from '../components/utils/premintmessage';
import {Link} from 'gatsby';

declare var window: any;
var provider: any;
var mobile: boolean;
var isWindowEth: boolean = false;

if(typeof window !== 'undefined' && window.ethereum) {
    isWindowEth = true;
    provider =  new ethers.providers.Web3Provider(window.ethereum);
    window.ethereum.on("chainChanged", (chainId:any) => {   
        window.location.reload();
    });
}
if(typeof window !== 'undefined') {
    var match = window.matchMedia("(max-width: 991px)");
    mobile = match.matches;
    match.addListener(() => {
        mobile = match.matches;
    });
}

const MintPage = () => {     
    const now = new Date();
    const [maxPerWallet, setMaxPerWallet] = useState(0);
    const [currentSaleType, setCurrentSaleType] = useState(SaleType.None);
    const [currentMainSaleType, setCurrentMainSaleType] = useState(SaleType.Private);
    const [walletAddress, setWalletAddress] = useState('');
    const [isConnected, setIsConnected] = useState(false);
    const [connectionMessage, setConnectionMessage] = useState('');
    const [numebrToMint, setNumberToMint] = useState(1);
    const [signer, setSigner] = useState();
    const [isMinting, setIsMinting] = useState(false);
    const [mintingSuccess, setMintingSuccess] = useState(false);
    const [alreadyMinted, setAlreadyMinted] = useState(0);
    const [mintingTransaction, setMintingTransaction] = useState('');
    const [mintError, setMintError] = useState('');
    const [totalSupply, setTotalSupply] = useState(0); 
    const [price, setPrice] = useState(''); 
    const [priceNumber, setPriceNumber] = useState(0);
    const [isWalletEligibleForPrivateSale, setIsWalletEligibleForPrivateSale] = useState(false);

    function formatWalletAddress(address:string) {
        if (address.length > 35) {
          return address.substr(0, 10) + '...' + address.substr(address.length-10, address.length);
        }
        return address;
    }
    function setStage(stage:number) {
        if(stage == 2) {
            setCurrentSaleType(SaleType.Private);
            setCurrentMainSaleType(SaleType.Private);
        } else if(stage == 1) {
            setCurrentSaleType(SaleType.Public);
            setCurrentMainSaleType(SaleType.Public);
        } else if(stage == 0) {                    
            if(now < Constants.CSHIRTS_PRIVATESALE_DATE) {
                setCurrentSaleType(SaleType.NotStarted);
            }
            else {
                setCurrentSaleType(SaleType.Pause);
            }
        }
    }

    React.useEffect(() => {
        async function updateAlreadyMinted() {
            const providerAlchemy = new ethers.providers.JsonRpcProvider(Constants.ALCHEMY_URL);
            const publicContract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, providerAlchemy);     
            Promise.all([
                publicContract.totalSupply(),
                publicContract._stage()
            ]).then(function(values) {
                setAlreadyMinted(ethers.BigNumber.from(values[0]).toNumber());  
                setStage(values[1]);
            });
        };

        async function getContractData() {
            const providerAlchemy = new ethers.providers.JsonRpcProvider(Constants.ALCHEMY_URL);
            const publicContract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, providerAlchemy); 

            Promise.all([
                publicContract.MAX_SUPPLY(),
                publicContract.totalSupply(),
                publicContract._price(),
                publicContract._stage(),
                publicContract._max_nft() // stage of the contract. type is int: {0 = PAUSED | 1 = ALLOWLIST | 2 = WAITLIST}    
            ]).then(function(values) {
                setStage(values[3]);
                setTotalSupply(ethers.BigNumber.from(values[0]).toNumber());
                setAlreadyMinted(ethers.BigNumber.from(values[1]).toNumber());
                setPrice(ethers.utils.formatEther(ethers.BigNumber.from(values[2]).toString()));
                setPriceNumber(parseFloat(ethers.utils.formatEther(ethers.BigNumber.from(values[2]))));
                setCurrentMainSaleType(now < Constants.CSHIRTS_PUBLICSALE_DATE ? SaleType.Private : SaleType.Public);
                setMaxPerWallet(values[4]);                
            });
        }

        getContractData();
        setInterval(() => {
            updateAlreadyMinted();
          }, process.env.NODE_ENV === "development" ? 15000 : 5000);
    }, []);

    let connectWallet;
    if(typeof window !== 'undefined')
    {        
         connectWallet = (async() => {
            if(window.ethereum) {           
                try {
                    setConnectionMessage('');
                    await provider.send('eth_requestAccounts', []);
                    var currentChainID = await provider.getNetwork();                    
                    if(currentChainID.chainId == Constants.CHAINID) {
                        const currentSigner = provider.getSigner();
                        setSigner(currentSigner);
                        const address = await currentSigner.getAddress();
                        if(address) {
                            setWalletAddress(address);
                            setIsConnected(true);   
                            if(signs[address]) {
                                setIsWalletEligibleForPrivateSale(true);
                            } 
                        }    
                        else { setWalletAddress(''); };
                    }
                    else { setConnectionMessage("Please select the " + Constants.CHAIN_NAME + " network in your wallet"); }
                } 
                catch(err) 
                { 
                    setConnectionMessage(err.message); 
                }  
            } 
            else { setConnectionMessage("Please install Metamask"); };
        });
    }

    const incremenetNumber = () => {
        if(numebrToMint < maxPerWallet) {
            setNumberToMint(numebrToMint + 1);
            var nbr = BigNumber.from(ethers.utils.parseEther(price)).mul(numebrToMint + 1);
            setPriceNumber(parseFloat(ethers.utils.formatEther(nbr)));
        }
    };

    const decremenetNumber = () => {
        if(numebrToMint > 1) {
            setNumberToMint(numebrToMint - 1);
            var nbr = BigNumber.from(ethers.utils.parseEther(price)).mul(numebrToMint - 1);
            setPriceNumber(parseFloat(ethers.utils.formatEther(nbr)));
        }
    };

    const onValueChange = () =>{};

    const mint =  (async() => {    
        if(typeof window !== 'undefined')
        {  
            if(window.ethereum && isConnected && walletAddress && alreadyMinted < totalSupply) {
                setIsMinting(true); 
                var signature = signs[walletAddress];
                var contract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, signer); 
                try {
                    var nftTx, gasLimit;
                    if(currentSaleType == SaleType.Private) {
                        if(signature) {
                            gasLimit = await contract.estimateGas.mintWaitlist(signature, numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint) });
                            gasLimit = gasLimit.add(gasLimit.mul(10).mod(100));
                            nftTx = await contract.mintWaitlist(signature, numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint), gasLimit: gasLimit });                
                        } else {
                            setMintingSuccess(false);
                            setIsMinting(false);
                            setMintError("You are NOT eligible for private sale");
                        }
                    }   
                    if(currentSaleType == SaleType.Public) {
                        gasLimit = await contract.estimateGas.mint(numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint)});
                        gasLimit = gasLimit.add(gasLimit.mul(10).mod(100));
                        nftTx = await contract.mint(numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint), gasLimit: gasLimit });
                    }  
                    if(nftTx) {            
                        setMintingTransaction(nftTx.hash);                               
                        try {
                            var receipt = await nftTx.wait();
                            if(receipt.status == 1) {
                                setMintingSuccess(true);
                                setIsMinting(false);
                            }
                        }
                        catch(txError) {    
                            setIsMinting(false);
                            setMintingSuccess(false);
                            setMintError("An error as occurred...");
                            console.log(txError);
                        
                            try {
                                const a = await provider.getTransaction(nftTx.hash);
                                let code = await provider.call(a as Deferrable<ethers.providers.TransactionRequest>, a.blockNumber)
                            } catch (txInternalError) {
                                console.log({txInternalError});
                                setMintError(txInternalError.reason);
                                
                                /* const code = err.data.replace('Reverted ','');                            
                                let reason = ethers.utils.toUtf8String('0x' + code.substr(138)); */
                            }
                        }
                        const alreadyMintedUpdated = await contract.totalSupply();
                        setAlreadyMinted(ethers.BigNumber.from(alreadyMintedUpdated).toNumber());                    
                    }
                }
                catch(error) {
                    setMintingSuccess(false);
                    setIsMinting(false);
                    setMintError(error.message);
                }            
            };
        }
    });

    const PageLayout = <Layout pageTitle="Non Fungible Cup: Mint" container="inner-page">
        <InnerPageTitle title="Mint your C-Shirts" subtitle="With your C-Shirts you can free mint tournaments NFTs, receive airdoprs and start earning ETH for every match win" />
        <section className="tf-section tf-item tf-mint">
            <div className="container">
                <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12">
                        <Supply privateSale={currentMainSaleType == SaleType.Private ? (alreadyMinted < Constants.CSHIRTS_PRIVATESALE_SUPPLY ? alreadyMinted : Constants.CSHIRTS_PRIVATESALE_SUPPLY) : Constants.CSHIRTS_PRIVATESALE_SUPPLY} 
                                privateSaleSupply={Constants.CSHIRTS_PRIVATESALE_SUPPLY}
                                publicSale={currentMainSaleType == SaleType.Public ? alreadyMinted : 0}
                                publicSaleSupply={Constants.CSHIRTS_PUBLICSALE_SUPPLY} 
                                price={price}/>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12">    
                        <div className="mint-box">
                            C-Shirts utilities:
                            <ul>
                                <li>Access to private sale (free-mint) on every tournament</li>
                                <li>Airdrops for tournament's new players</li>
                                <li>Access to discord private channels</li>
                                <li>Possibility to vote for new tournaments</li>
                            </ul>                            
                        </div>   
                        <div className="mint-box mint-box-minheight">      
                            <div className={isConnected || currentSaleType == SaleType.NotStarted || currentSaleType == SaleType.Pause ? "hide" : "mint-overlay"}>
                                <div className={mobile == false || isWindowEth == true ? "center mint-connectwallet" : "hide"}>
                                    <button className={isConnected ? "hide" : "tf-button btn-effect active btn-connectwallet"} onClick={connectWallet}>
                                        <span className="boder-fade"></span>          
                                        <span className="effect"><img src={MetamaskIcon} />Connect wallet</span>
                                    </button><br />
                                    <div className="mint-connectwallet-messages"><strong>{connectionMessage}</strong></div>
                                </div>                                
                                <div className={mobile == true && isWindowEth == false ? "center mint-connectwallet" : "hide" }>
                                    <a href={Constants.METAMASK_MOBILE_MINTURL} className={isConnected ? "hide" : "tf-button btn-effect active btn-connectwallet"}>
                                        <span className="boder-fade"></span>          
                                        <span className="effect"><img src={MetamaskIcon} />Open Metamask</span>
                                    </a><br />
                                    <div className="mint-connectwallet-messages"><strong>{connectionMessage}</strong></div>
                                </div>
                            </div>                          
                            <div className={currentSaleType == SaleType.NotStarted || currentSaleType == SaleType.Pause ? "mint-container" : "hide"}>
                                <div className={currentSaleType == SaleType.Pause ? "mint-container toUpper center" : "hide"}>
                                    <h5>MINT PAUSED</h5>
                                </div>
                                <div className={currentSaleType == SaleType.NotStarted ? "mint-container toUpper center" : "hide"}>
                                    <h5>Mint will start at {currentSaleType == SaleType.Private ? Constants.CSHIRTS_PRIVATESALE_DATE.toUTCString() : Constants.CSHIRTS_PUBLICSALE_DATE.toUTCString()}</h5>
                                    <div className="mint-container">
                                        <a className="tf-button btn-effect active" href={Constants.DISCORD}>   
                                            <span className="boder-fade"></span>                                     
                                            <span className="effect">Join Discord</span>
                                        </a>
                                    </div>
                                </div>                                
                            </div>                            
                            <div className={alreadyMinted >= Constants.CSHIRTS_PRIVATESALE_SUPPLY && currentSaleType == SaleType.Private ? "mint-container" : "hide"}>
                                <div className="mint-container center">
                                    <h5>PRIVATE SALE MINTED OUT</h5><br />
                                    Public sales start on {Constants.CSHIRTS_PUBLICSALE_DATE.toLocaleString()}
                                </div>
                            </div>
                            <div className={alreadyMinted >= Constants.CSHIRTS_PUBLICSALE_SUPPLY && currentSaleType == SaleType.Public ? "mint-container" : "hide"}>
                                <div className="mint-container center">
                                    <h5>COLLECTION MINTED OUT</h5>
                                </div>                               
                            </div>
                            <div className={alreadyMinted >= Constants.CSHIRTS_PRIVATESALE_SUPPLY && currentSaleType == SaleType.Private || currentSaleType == SaleType.None ? "hide" : ""}>
                                <div className={alreadyMinted >= Constants.CSHIRTS_PUBLICSALE_SUPPLY && currentSaleType == SaleType.Public ? "hide" : ""}>
                                    <div className={currentSaleType == SaleType.NotStarted || currentSaleType == SaleType.Pause ? "hide" : ""}>
                                        <h4>{currentMainSaleType == SaleType.Public ? "Public sale" : "Private sale"}</h4>
                                        <div className={isConnected ? "wallet" : "hide"}>Connected wallet: {formatWalletAddress(walletAddress)}</div>
                                        <div className="mint-container">                                
                                            <div className="d-flex justify-content-center">
                                                <div className="d-flex flex-row">
                                                    <div className="p-2">
                                                        <button onClick={decremenetNumber} disabled={isConnected == false || numebrToMint - 1 == 0 || mintingSuccess || isMinting || (currentSaleType == SaleType.Private && isWalletEligibleForPrivateSale == false)} className="increment-decrement">-</button>
                                                    </div>
                                                    <div className="p-2">
                                                        <input type="text" value={numebrToMint} onChange={onValueChange} disabled={isConnected == false || mintingSuccess || isMinting || (currentSaleType == SaleType.Private && isWalletEligibleForPrivateSale == false)}></input>
                                                    </div>
                                                    <div className="p-2">
                                                        <button onClick={incremenetNumber} disabled={isConnected == false || numebrToMint == maxPerWallet || mintingSuccess || isMinting || (currentSaleType == SaleType.Private && isWalletEligibleForPrivateSale == false)} className="increment-decrement">+</button>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="center">Price: {priceNumber} ETH + gas</div>
                                            <div className="mint-button center">
                                                <button className="tf-button btn-effect active" onClick={mint} disabled={isConnected == false || mintingSuccess || isMinting || (currentSaleType == SaleType.Private && isWalletEligibleForPrivateSale == false)}>
                                                    <span className="boder-fade"></span>          
                                                    <span className="effect">MINT</span>
                                                </button> 
                                                <div className={isMinting ? 'mint-messages mint-waiting' : 'hide'}>                                    
                                                    Transaction sent! Wait for confirmation<br />
                                                    <a href={Constants.SCAN_URL + mintingTransaction} title="See transaction" target="_blank">View on etherscan</a>
                                                </div>
                                                <div className={mintingSuccess ? 'mint-messages mint-success' : 'hide'}>
                                                    Mint succeeded!<br />
                                                    <a href={Constants.SCAN_URL + mintingTransaction} title="See transaction" target="_blank">View on etherscan</a>
                                                </div>
                                                <div className={mintError ? 'mint-error mint-messages': 'hide'}>
                                                    Something went wrong: {mintError}
                                                </div>
                                                <div className={isConnected && isWalletEligibleForPrivateSale == true && currentSaleType == SaleType.Private && isMinting == false && !mintError && mintingSuccess == false ? "mint-messages mint-success" : "hide"}>
                                                    You are eligible for the private sale!
                                                </div>
                                                <div className={isConnected && isWalletEligibleForPrivateSale == false && currentSaleType == SaleType.Private && isMinting == false && !mintError && mintingSuccess == false ? "mint-messages mint-error" : "hide"}>
                                                    You are NOT eligible for the private sale
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className={isConnected ? "hide" : "row"}>
                            <div className="col-md-12 center">
                                
                            </div>
                        </div> 
                    </div>
                </div>
            </div>
        </section>
        <NextDrops background='tf-nextdrops' />
        <FAQ />
    </Layout>;

    const PremintPageLayout = <Layout pageTitle="Non Fungible Cup: Mint" container="inner-page">
        <InnerPageTitle title="Mint your C-Shirts" subtitle="With your C-Shirts you can free mint tournaments NFTs, receive airdoprs and start earning ETH for every match win" />
        <section className="tf-section tf-item">
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="info-box">
                            C-Shirts utilities.
                            <ul>
                                <li>Lifetime access to the free-minting private sale of each Tournament drop</li>
                                <li>Receive airdrop of new Tournament C-Shirts</li>
                                <li>The right to vote about next year's tournament planning</li>
                            </ul>
                        </div> 
                    </div>
                    <div className="col-md-6">
                        <div className="info-box">
                            How to get a C-Shirt?
                            <ul>
                                <li><Link to="/premint">Reserve a spot</Link> in the WL to access the free-minting <Link to="/mint">private sale</Link> of {Constants.CSHIRTS_PRIVATESALE_SUPPLY} C-Shirts (1 per wallet).</li>
                                <li>Mint the other {Constants.CSHIRTS_PUBLICSALE_SUPPLY - Constants.CSHIRTS_PRIVATESALE_SUPPLY} C-Shirts at the <Link to="/mint">public sale</Link> (no limit for each wallet)</li>
                                <li>Check the secondary market on <a href={Constants.CSHIRTS_OPENSEALINK}>Opensea</a></li>
                            </ul>
                        </div> 
                    </div>
                </div>
                <div className="col-md-12 center">
                    <PreMintMessage />
                </div>
            </div>
        </section>
    </Layout>
    return(
        Constants.PREMINT && process.env.NODE_ENV !== "development" ? PremintPageLayout : PageLayout
    );
};

export default MintPage;